.warningContainer {
    background: #212121;
    height: 100%;
    max-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.warningBox {
    border: 5px solid whitesmoke;
    color: whitesmoke;
    font-family: "Ubuntu-Bold";
    text-align: center;
    padding: 30px 20px 30px 20px;
}

.startContainer {
    border: 3px solid whitesmoke;
    width: fit-content;
    padding: 2px 5px 3px 5px;
    margin: 30px auto 0px auto;
    cursor: pointer;
}

.landscapeWarning {
    color: orange;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .warningBox {
        width: 80%;
    }
}

@media only screen and (min-width: 768px) {
    /* For mobile phones: */
    .warningBox {
        width: 30%;
    }
}